.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-color);
  color: var(--text1-color);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover-color);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  background: linear-gradient(255deg, var(--instagram-end) 0%, var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text2-color);
  font-weight: bold;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: normal;
  color: var(--text1-color);
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: bold;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
.header {
  display: flex;
  flex-wrap: wrap;
  margin: 2.25rem auto 0;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text2-color);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  align-items: center;
  gap: 0.5rem;
  border: none;
  margin: 0;
}
.toggle legend {
  font-size: 0.875rem;
  grid-column: 1/2;
  float: left;
  color: var(--toggle-color);
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  height: 1.5rem;
  width: 1.5rem;
}
.toggle input[type=radio]:hover ~ .toggle__button {
  border: 2px solid var(--text2-color);
}
.toggle__background {
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-start-color) 0%, var(--toggle-bg-end-color) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--background);
  transition: all 400ms cubic-bezier(0.85, 0, 0.15, 1);
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text1-color);
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text2-color);
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --ver: hsl(0, 0%, 100%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-tp-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --dark-toggle-bg-start: hsl(210, 78%, 56%);
  --dark-toggle-bg-end: hsl(146, 68%, 55%);
  --dark-toggle: hsl(230, 19%, 60%);
  --light-bg: hsl(0, 0%, 100%);
  --light-tp-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --light-toggle-bg-start: hsl(0, 0%, 73%);
  --light-toggle-bg-end: hsl(0, 0%, 73%);
  --light-toggle: hsl(230, 19%, 60%);
  --background: var(--light-bg);
  --text1-color: var(--light-text1);
  --text2-color: var(--light-text2);
  --card-color: var(--light-card);
  --card-hover-color: var(--light-card-hover);
  --toggle-bg-start-color: var(--light-toggle-bg-start);
  --toggle-bg-end-color: var(--light-toggle-bg-end);
  --toggle-color: var(--light-toggle);
}

body.light {
  --background: var(--light-bg);
  --text1-color: var(--light-text1);
  --text2-color: var(--light-text2);
  --card-color: var(--light-card);
  --card-hover-color: var(--light-card-hover);
  --toggle-bg-start-color: var(--light-toggle-bg-start);
  --toggle-bg-end-color: var(--light-toggle-bg-end);
  --toggle-color: var(--light-toggle);
}

body.dark {
  --background: var(--dark-bg);
  --text1-color: var(--dark-text1);
  --text2-color: var(--dark-text2);
  --card-color: var(--dark-card);
  --card-hover-color: var(--dark-card-hover);
  --toggle-bg-start-color: var(--dark-toggle-bg-start);
  --toggle-bg-end-color: var(--dark-toggle-bg-end);
  --toggle-color: var(--dark-toggle);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}